<template>
    <div class="home">
        <el-dropdown>
            <el-button>
                {{i18n.$t('home').switchLanguage}}
                <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <template #dropdown>
                <el-dropdown-menu>
                    <el-dropdown-item @click="switchLanguage('cn')">中文</el-dropdown-item>
                    <el-dropdown-item @click="switchLanguage('en')">English</el-dropdown-item>
                    <el-dropdown-item @click="switchLanguage('jp')">日本語</el-dropdown-item>
                </el-dropdown-menu>
            </template>
        </el-dropdown>
        <el-row type="flex" justify="center">
            <el-col :span="24" class="title" align="middle">Qbay</el-col>
        </el-row>
        <router-view :form="form"></router-view>
    </div>
</template>

<script>
import { useI18n } from "../../i18nPlugin";
export default {
    name: "home",
    setup() {
        const i18n = useI18n();
        return {
            i18n
        };
    },
    // 注入app.vue中设定好的方法
    inject: ["reload"],
    data() {
        return {
            form: {},
            flag: false,
            value: ""
        };
    },
    mounted() {
        if (this.$route.query.data) {
            this.form = JSON.parse(this.$route.query.data);
        }
    },
    methods: {
        switchLanguage(params) {
            window.localStorage.setItem("language", params);
            this.i18n.locale.value = params;
            // 刷新页面
            this.reload();
        }
    }
};
</script>

<style lang="scss" scoped>
.title {
    min-height: 50px;
    line-height: 50px;
    color: #ffffff;
    font-weight: bold;
    font-size: 70px;
    margin-top: 100px;
}
.home {
    width: 100%;
    height: 400px;
    background-color: #f0c9c9;
    position: relative;
}
.color {
    background-color: #68bf99;
    border: 1px solid #1fab89;
}
</style>
